/*aninmations are here*/

/*create  a animation to pop up the image when hovered*/

@keyframes pop {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.1);
  }
  0% {
    transform: scale(1);
  }
}

/*shake icon when heart is clicked*/
@keyframes shakeh {
  0% {
    transform: translate(0, 0);
  }
  10% {
    transform: translate(-10px, 0);
  }
  20% {
    transform: translate(10px, 0);
  }
  30% {
    transform: translate(-10px, 0);
  }
  40% {
    transform: translate(10px, 0);
  }
  50% {
    transform: translate(-10px, 0);
  }
  60% {
    transform: translate(10px, 0);
  }
  70% {
    transform: translate(-10px, 0);
  }
  80% {
    transform: translate(10px, 0);
  }
  90% {
    transform: translate(-10px, 0);
  }
  100% {
    transform: translate(0, 0);
  }
}


/*scroll the text horizonatlly in the footer*/
@keyframes scroll1 {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(100%, 0);
  }
}

/*shake verticaly when heart is clicked*/
@keyframes shake {
  0% {
    transform: translate(0, 0);
  }
  10% {
    transform: translate(0, -10px);
  }
  20% {
    transform: translate(0, 10px);
  }
  30% {
    transform: translate(0, -10px);
  }
  40% {
    transform: translate(0, 10px);
  }
  50% {
    transform: translate(0, -10px);
  }
  60% {
    transform: translate(0, 10px);
  }
  70% {
    transform: translate(0, -10px);
  }
  80% {
    transform: translate(0, 10px);
  }
  90% {
    transform: translate(0, -10px);
  }
  100% {
    transform: translate(0, 0);
  }
}